

const tabs = () => {

//TABS START
const showAllTab = document.querySelector('.show-all')
const webTab = document.querySelector('.web')
const seoTab =  document.querySelector('.seo')
const smmTab = document.querySelector('.smm')
const allFilteredDivs = document.querySelectorAll('.filtered-div')
const tabToggle = document.querySelectorAll('.tab-toggle')
// Turns node list into an array
const allFilteredDivsArray = Array.from(allFilteredDivs)

if (showAllTab){

   tabToggle.forEach((item) => { 
        item.addEventListener('click', ()=>{
            tabToggle.forEach((child)=>{
                child.classList.remove('activeClass')
            });
            item.classList.add('activeClass');
        })
    });


showAllTab.addEventListener('click', showAll)
function showAll() { // показать ВСЕ блоки
  allFilteredDivsArray.forEach(div => {
    div.classList.remove('remove')
    div.classList.add('show')
    
  })
}

webTab.addEventListener('click', showWeb)
function showWeb() {
  allFilteredDivsArray.forEach(div => {
    div.classList.remove('remove')

    if (!div.dataset.web) { // скрываем все блоки без dataset WEB
      div.classList.add('remove')
    }

  })
}

seoTab.addEventListener('click', showSeo)
function showSeo() {
  allFilteredDivsArray.forEach(div => {
    div.classList.remove('remove')

    if (!div.dataset.seo) {
      div.classList.add('remove')
    }

  })
}

smmTab.addEventListener('click', showSmm)
function showSmm() {
  allFilteredDivsArray.forEach(div => {
    div.classList.remove('remove')
    
    if (!div.dataset.smm) {
      div.classList.add('remove')
    }

  })
}
}

//TABS END
}
export default tabs;