

$(document).ready(function () {
let slider1 = document.querySelector(".slider");
let articleSlider = document.querySelector(".article-slider");

if (slider1) {
    $('.slider').slick({
        infinite: true,
        slidesToShow: 1,
        slidesToScroll: 1,
        arrows: true,
        dots: true,
        autoplay: false,
        autoplaySpeed: 1500,
        customPaging: function (slider, i) {
            return '<div class="dots-block"><div class="dotscustom"></div></div>';
        },
        prevArrow: '<div class="arrow arrow-left"><img class="arrow-img arrow-img--left" src="/src/assets/images/arrow-left.svg"></div>',
        nextArrow: '<div class="arrow arrow-right"><img class="arrow-img arrow-img--right" src="/src/assets/images/arrow-right.svg"></div>',
        responsive: [
            {
              breakpoint: 640,
              settings: {
                arrows: false
            }
          }
         
          ]


    });
}

let sliderEmployee = document.querySelector('.slider-employee');
if(sliderEmployee) {
    $('.slider-employee').slick({
        infinite: true,
        slidesToShow: 3,
        slidesToScroll: 1,
        arrows: true,
        dots: false,
        autoplay: true,
        autoplaySpeed: 1500,
        prevArrow: '<div class="arrow arrow-left"><img class="arrow-img arrow-img--left" src="/src/assets/images/arrow-left.svg"></div>',
        nextArrow: '<div class="arrow arrow-right"><img class="arrow-img arrow-img--right" src="/src/assets/images/arrow-right.svg"></div>'


    });
}

let slider2 = document.querySelector(".slider2");
if (slider2) {
$('.slider2').slick({
    asNavFor: '.slider-nav',
    infinite: true,
    slidesToShow: 1,
    slidesToScroll: 1,
    arrows: true,
    dots: false,
    autoplay: true,
    autoplaySpeed: 1500,
    prevArrow: '<div class="arrow arrow-left"><img class="arrow-img arrow-img--left" src="/src/assets/images/arrow-left.svg"></div>',
    nextArrow: '<div class="arrow arrow-right"><img class="arrow-img arrow-img--right" src="/src/assets/images/arrow-right.svg"></div>'
  });
  $('.slider-nav').slick({
    slidesToShow: 2,
    slidesToScroll: 1,
    asNavFor: '.slider2',
    dots: true,
    autoplay: true,
    focusOnSelect: true,
    arrows:false,
    customPaging: function (slider, i) {
        return '<div class="dots-block"><div class="dotscustom"></div></div>';
    },
  });
}


if(articleSlider) {
    $(window).on('load resize', function() {
        if ($(window).width() < 650) {
          $('.article-slider:not(.slick-initialized)').slick({
            // asNavFor: '.slider-nav',
            infinite: true,
            slidesToShow: 1,
            slidesToScroll: 1,
            arrows: false,
            dots: true,
            autoplay: true,
            autoplaySpeed: 1500,
            customPaging: function (slider, i) {
                return '<div class="dots-block"><div class="dotscustom"></div></div>';
            },
            responsive: [
                {
                  breakpoint: 640,
                  settings: {
                    slidesToShow: 1,
                    slidesToScroll: 1,
                    autoplay: false,
                    customPaging: function (slider, i) {
                        return '<div class="dots-block"><div class="dotscustom"></div></div>';
                    }
                }
              }
             
              ]
    
})
}    })
}

})