const forms = (state) => { //указываю state чтобы была возможность передавать в форму данные из state при вызове этой функции
    const contactForm = document.querySelectorAll('.send-form');
    const popupFunc = document.querySelector('.popup_func');
    const message = {
        loading: 'Download...',
        success: 'Thank you! We will contact you soon',
        failure: 'Something went wrong...'
    };

    //функция отправки на сервер start
    const postData = async (url, data) => { // функция отправки формы на сервер
        document.querySelector('.status').textContent = message.loading; // выводим текст в поле статуса
        let res = await fetch(url, { // Отправка формы, записал результат ПОСЛЕ отправки
            method: "POST",
            body: data
        });

        return await res.text(); // получаем ответ в текстовом виде
    };
    //функция отправки на сервер end


contactForm.forEach(item => { // проходимся по всем формам
    item.addEventListener('submit', (e) => {
        e.preventDefault(); // чтобы страница не перезагружалась при отправке

        let statusMessage = document.querySelectorAll('.status'); //статус отправки формы
        statusMessage.forEach(statusMess=>{
            statusMess.classList.add('visible');
        
        
        // item.appendChild(statusMessage); // выводим наш statusMessage внутри формы

        const formData = new FormData(item); // объект FormData сохраняет в себе даные ВСЕХ инпутов формы, даже картинки и файлы
        
        //отправляю форму на сервер
        postData('/mail.php', formData) // отправляю на сервер(assets/server.php) все данные(formData) формы
            .then(res => {
                console.log(res);
                statusMess.textContent = message.success; // выводим сообщение о успешной отправке
            })
            .catch(() => statusMess.textContent = message.failure)// сообщение об ошибке при ошибке
            .finally(() => { //после отправки формы
                // clearInputs(); //очищаем инпуты
                setTimeout(() => {
                    statusMess.classList.remove('visible');
                    popupFunc.style.top="-3000px";
                    document.body.style.overflow = "";
                    
                }, 3000); //удалить поле статуса отправки через 3 сек
                
            });


    });
})
});
}
export default forms;